@import url("https://fonts.googleapis.com/css2?family=Jost:ital,wght@0,400;0,500;1,500&family=Satisfy&display=swap");

.app-container {
  display: grid;
  grid-template-columns: 10% repeat(3, minmax(5rem, 1fr)) 10%;
}

.form-container {
  grid-column: 2/5;
  align-self: flex-end;
  background-color: rgb(253, 251, 236);
  border: 10px solid black;
  padding: 1rem;
  border-color: orange;
  border-width: 10px;
  border-radius: 15px 15px 15px 15px;
}

.form-title {
  margin: auto;
  align-content: center;
  font-family: "Satisfy", cursive;
  text-align: center;
}

.recipe-title {
  margin: auto;
  align-content: center;
  font-family: "Jost", normal;
  text-align: center;
}

p {
  font-family: "Jost", normal;
  text-align: left;
  font-size: 1.2rem;
}

h3 {
  font-family: "Jost", normal;
  text-align: left;
  font-size: 1.4rem;
  text-decoration-line: underline;
}

ul {
  font-family: "Jost", normal;
  text-align: left;
  font-size: 1.2rem;
}

.recipe-img {
  grid-column: 3/4;
  align-self: flex-end;
  object-fit: cover;
  width: 100%;
  align-content: center;
  max-block-size: 40rem;
}

.button {
  justify-content: center;
  align-content: center;
  color: #494949 !important;
  text-transform: uppercase;
  text-decoration: none;
  background: #ffffff;
  padding: 10px;
  border: 4px solid #494949 !important;
  margin: 1rem;
  transition: all 0.4s ease 0s;
  position: relative;
}

.button:hover {
  color: #ffffff !important;
  background: #f6b93b;
  border-color: #f6b93b !important;
  transition: all 0.4s ease 0s;
}

.select-css {
  grid-column: 3/4;
  font-size: 16px;
  font-family: sans-serif;
  font-weight: 700;
  color: #444;
  line-height: 1.3;
  padding: 0.6em 1.4em 0.5em 0.8em;
  width: 80%;
  max-width: 80%;
  box-sizing: border-box;
  margin-left: 10%;
  margin-right: 10%;
  border: 1px solid #aaa;
  box-shadow: 0 1px 0 1px rgba(0, 0, 0, 0.04);
  border-radius: 0.5em;
  -moz-appearance: none;
  -webkit-appearance: none;
  appearance: none;
  background-color: #fff;
  background-image: url("data:image/svg+xml;charset=US-ASCII,%3Csvg%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20width%3D%22292.4%22%20height%3D%22292.4%22%3E%3Cpath%20fill%3D%22%23007CB2%22%20d%3D%22M287%2069.4a17.6%2017.6%200%200%200-13-5.4H18.4c-5%200-9.3%201.8-12.9%205.4A17.6%2017.6%200%200%200%200%2082.2c0%205%201.8%209.3%205.4%2012.9l128%20127.9c3.6%203.6%207.8%205.4%2012.8%205.4s9.2-1.8%2012.8-5.4L287%2095c3.5-3.5%205.4-7.8%205.4-12.8%200-5-1.9-9.2-5.5-12.8z%22%2F%3E%3C%2Fsvg%3E"),
    linear-gradient(to bottom, #ffffff 0%, #e5e5e5 100%);
  background-repeat: no-repeat, repeat;
  background-position: right 0.7em top 50%, 0 0;
  background-size: 0.65em auto, 100%;
}
.select-css::-ms-expand {
  display: none;
}
.select-css:hover {
  border-color: #888;
}
.select-css:focus {
  border-color: #aaa;
  box-shadow: 0 0 1px 3px rgba(59, 153, 252, 0.7);
  box-shadow: 0 0 0 3px -moz-mac-focusring;
  color: #222;
  outline: none;
}
.select-css option {
  font-weight: normal;
}
